import { http } from "@/helpers/http";
import Vue from 'vue'

export default {
    namespaced: true,
    state: {
        dashboardData: null,
        products: null,
        orders: null,
        salesAgents: null,
        settings: null
    },

    getters: {
        products(state) {
            return state.products;
        },
        salesAgents(state) {
            return state.salesAgents;
        },
        orders(state) {
            return state.orders;
        }
    },

    mutations: {

        setDashboardData(state, dashboardData) {
            state.dashboardData = dashboardData;
        },

        setProducts(state, products) {
            Vue.set(state, 'products', products);
            // state.products = products;
        },

        setOrders(state, orders) {
            Vue.set(state, 'orders', orders);
        },

        setSalesAgents(state, salesAgents) {
            // state.salesAgents = salesAgents;
            Vue.set(state, 'salesAgents', salesAgents);
        },

    },
    actions: {
        fetchProducts({ state, commit, rootState }) {
            http.get("/products").then(response => {
                commit('setProducts', response.data);
            })
        },
        fetchOrders({ state, commit, rootState }) {
            http.get("/orders").then(response => {
                commit('setOrders', response.data);
            })
        },
        fetchSalesAgent({ state, commit, rootState }) {
            http.get("/sales_agents").then(response => {
                commit('setSalesAgents', response.data);
            })
        },
        fetchProductDetail({ state, commit, rootState }, payload) {
            http.get("/products/" + payload.id).then(response => {
                payload.callback(response.data);
            })
        },
        fetchSalesAgentDetail({ state, commit, rootState }, payload) {
            http.get("/sales_agents/" + payload.id).then(response => {
                payload.callback(response.data);
            })
        },
        fetchOrderDetail({ state, commit, rootState }, payload) {
            http.get("/orders/" + payload.id).then(response => {
                payload.callback(response.data);
            })
        },
        createProduct({ state, commit, rootState }, payload) {
            http.post("/products/", payload.product).then(response => {
                payload.callback(response.data);
            })
        },
        createOrder({ state, commit, rootState }, payload) {
            http.post("/orders/", payload.order).then(response => {
                payload.callback(response.data);
            })
        },
        createSalesAgent({ state, commit, rootState }, payload) {
            http.post("/sales_agents/", payload.salesAgent).then(response => {
                payload.callback(response.data);
            })
        },
        updateProduct({ state, commit, rootState }, payload) {
            http.put("/products/" + payload.product.id, payload.product).then(response => {
                payload.callback(response.data);
            })
        },
        updateSalesAgent({ state, commit, rootState }, payload) {
            http.put("/sales_agents/" + payload.salesAgent.id, payload.salesAgent).then(response => {
                payload.callback(response.data);
            })
        },
        updateOrder({ state, commit, rootState }, payload) {
            http.put("/orders/" + payload.order.id, payload.order).then(response => {
                payload.callback(response.data);
            })
        },
        deleteProduct({ state, commit, rootState }, payload) {
            http.delete("/products/" + payload.id).then(response => {
                let products = state.products;
                let index = products.findIndex(item => item.id == payload.id);
                products.splice(index, 1);
                commit('setProducts', products);
            })
        },
        deleteSalesAgent({ state, commit, rootState }, payload) {
            http.delete("/sales_agents/" + payload.id).then(response => {
                let salesAgents = state.salesAgents;
                let index = salesAgents.findIndex(item => item.id == payload.id);
                salesAgents.splice(index, 1);
                commit('setSalesAgents', salesAgents);
            })
        },
        deleteOrder({ state, commit, rootState }, payload) {
            http.delete("/orders/" + payload.id).then(response => {
                let orders = state.orders;
                let index = orders.findIndex(item => item.id == payload.id);
                orders.splice(index, 1);
                commit('orders', orders);
            })
        }

    }
}